<template>
    <div class="one-cart-item">
        <div class="left" v-loading="loadingImg">
            <img :src="img" @click="goToDetailsPage"/>
        </div>
        <div class="right">
            <div class="text">
                <h3 class="title" @click="goToDetailsPage">{{title}}</h3>
                <p class="content">{{content}}</p>
                <span class="price">
                    <span class="price-number">{{price}}</span>
                </span>
            </div>
            <div class="cart-btn">
                <my-input-number :count="counter" @changeNumberEvent="getOperator"></my-input-number>
            </div>
        </div>
    </div>
</template>

<script>
    import "../../assets/js/loading.js";
    export default {
        name: 'one-commodity',
        props: ['id','img', 'title', 'content', 'price', 'count'],
        data (){
            return {
                loadingImg: true,
                oneCommodity: {
                    id: this.id,
                    img: this.img,
                    title: this.title,
                    content: this.content,
                    price: this.price,
                    count: this.count,
                    isInCart: false,
                }
            }
        },
        computed: {
            counter (){
                let result = 0;
                let cartGoods = this.$store.state.cartGoods;

                /*Array some()
                some() 
                    true , 
                    false
                 some() 
                 some() */

                cartGoods.some(good => {
                    if (good.id === this.id) {
                        result = good.count;
                    }
                });
                return result;
            }
        },
        methods: {
            getOperator (op) {
                let id = this.id;
                if (op === 'plus') {
                    this.$store.commit('addGoods', id);
                } else {
                    if (this.counter === 1) {//1 
                        this.$store.commit('deleteGoodsFromCart', id);
                    } else {
                        this.$store.commit('reduceGoods', id);
                    }
                }
            },
            goToDetailsPage () {
                this.$router.push({
                    path: '/DetailsPage',
                    query: this.oneCommodity
                });
                //
                this.$store.state.cartCounter++;
                this.$store.state.cartCounter--;
            },
        },
        created () {
            let img = new Image();
            img.src = this.img;
            img.onload = () => {
                this.loadingImg = false;
            }
        }

    }
</script>

<style lang="stylus" scoped>
    @import '../../assets/css/variable.styl';
    .one-cart-item
        height: $OneCommodityWrapHeight
        width: 100%
        font-size: 0
        border-bottom: 1px solid #e4e7ed
        background-color: #fff
        .left
            width: 40%
            height: 100%
        .left>img
            height: 80%
            transform: translateY(10%)
        .right
            box-sizing: border-box
            position: relative
            height: 100%
            width: 60%
            text-align: left
            padding-left: 10px
            .text
                position: absolute
                line-height: 30px
                top: 50%
                transform: translateY(-50%)
                .price
                    .price-number
                        color: $mainOrange
                        font-size: 18px
                        font-weight: 600
            .cart-btn
                position: absolute
                bottom: 5px
                right: 10px
    .one-cart-item>div
        font-size: $GobalFontSize
        display: inline-block
        vertical-align: middle
    @media screen and (min-width: 600px) {
        .one-cart-item{
            height: $OneCommodityWrapHeight600;
        }
    }
    @media screen and (min-width: 800px) {
        .one-cart-item{
            height: $OneCommodityWrapHeight800;
        }
    }
    @media screen and (min-width: 1025px) {
        .one-cart-item{
            height: $OneCommodityWrapHeight1025;
        }
    }
</style>
