import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
      cartGoods: [], // 
      cartCounter: 0, // 
      GoodsCurrentSelKind: 0,// 
      username: '', // 
      isLogin: false,
      address:[
          {
              name: '',
              phone: 13568978546,
              city: '',
              detailedAddress: '111'
          },
          {
              name: '',
              phone: 13568911546,
              city: '',
              detailedAddress: '111'
          },
      ],
  },
  mutations: {
      //
      changeCurrentSelKind(state, kind){
          state.GoodsCurrentSelKind = kind;
      },
      addGoodsToCart (state:any, item:any) {
          item.isInCart = true;
          item.count++;
          state.cartGoods.push(item);
          state.cartCounter++;
      },
      deleteGoodsFromCart(state:any, itemId:number) {
          state.cartCounter--;
          state.cartGoods.some((val:any, index:number, Goods:any) => {
              if (val.id === itemId) {
                  val.isInCart = false;
                  val.count--;
                  Goods.splice(index, 1);
                  return true;
              }
          })
      },
      addGoods(state:any, itemId:number){
          state.cartCounter++;
          state.cartGoods.some(val => {
              if (val.id === itemId) {
                  val.count++;
                  return true;
              }
          })
      },
      reduceGoods(state:any, itemId:number){
          state.cartCounter--;
          state.cartGoods.some(val => {
              if (val.id === itemId) {
                  val.count--;
                  return true;
              }
          })
      },
      deleteAllFromCart(state:any, cartGoods:string){
          state.cartCounter = 0;
          state.cartGoods = [];
      },
      login(state, username){
          state.username = username;
          state.isLogin = true;
      },
      logout(state){
          state.isLogin = false;
      },

  },
  actions: {

  },
});
